body {
  font-family: 'Lucida Sans Unicode';
  background-color:black;
}

h1 {
  text-align: center;
  font-size: 50px;
}

table {

  margin: 0 auto;
  border-spacing: 0px 0px;
}

.button {
  left: 43%;
  position: absolute;
  color: #50C8FF;
  height: 68px;
  width: 300px;
  border-radius: 5px;
  margin: 0 auto;
  margin-bottom: 25px;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: background-color 0.5s, color 0.5s;
  bottom: 37px;
  font-size: 35px;
  border: 3px solid white;


}

.button:hover {
  background-color: white;
  color: #303030;
  transition: background-color 0.5s, color 0.5s;
}

.cell {
      background: url(https://i.redd.it/v4h59jq252y01.jpg) no-repeat center;
      height: 120px;
      width: 120px;
      /* background-color: #1990ff; */
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      cursor: pointer;
}

.circle {
  height: 25px;
  width: 25px;
  border: 1px solid #000;
  border-radius: 100px;
}

.circles {
  margin: -40px 0 25px 0;
  display: flex;
  justify-content: center;
}

.red-sm {
  background-color: red;
}

.yellow-sm {
  background-color: yellow;
}


.winnerMessage {

  position: relative;
  bottom: 536px;


}


.message {
      margin: 0;
      color: white;
      /* bottom: 548px; */
      position: relative;
      text-align: center;
      font-size: 96px;
      left: 0px;
      background-color: #f70505;
      padding: 0;
}

.white {
  height: 90px;
  width: 90px;
  background-color: #FFF;
  border-radius: 100px;
}

.red {
  height: 90px;
  width: 90px;
  background-color: red;
  border-radius: 100px;
  transition: background-color 0.5s;
}

.yellow {
  height: 90px;
  width: 90px;
  background-color: yellow;
  border-radius: 100px;
  transition: background-color 0.5s;
}
